import React from "react";
import "./ConfiguratorControls.css";
import modelGrau from "../img/model-grau.jpg";
import modelLuxus from "../img/model-luxus.jpg";
import modelWeiss from "../img/model-weiss.jpg";

const ConfiguratorControls = () => (
  <div className="ConfiguratorControls">
    <div className="navigation">
      <div className="navigation-item">
        <i className="fas fa-home icon"></i>
        <div className="tooltip-content">
          <p>
            Schlafzimmer <i className="fas fa-check-circle check"></i>
          </p>
          <p>
            Wohnzimmer <i className="fas fa-check-circle check"></i>
          </p>
          <p>Kinderzimmer</p>
          <p>
            <span className="underline">Küche</span>
          </p>
          <p>Flur</p>
          <button className="finish-configurator">
            Konfiguration fertigstellen
          </button>
        </div>
      </div>
      <div className="navigation-item">
        <i className="fas fa-couch icon"></i>
        <span className="tooltip-content">
          <p>
            Herd <i className="fas fa-check-circle check"></i>
          </p>
          <p>
            Fronten <i className="fas fa-check-circle check"></i>
          </p>
          <p>Kühlschrank</p>
        </span>
      </div>
    </div>
    <div className="models">
      <div className="model-item">
        <img src={modelGrau} alt="Model Grau" />
        <i className="fas fa-check-circle check center"></i>
        <span className="tooltip-content">
          Model Grau <br />
          Ganz tolle graue Oberfläche <br />
          Preis: 1000 € <br />
          Lieferbar: ab sofort
        </span>
      </div>
      <div className="model-item">
        <img src={modelLuxus} alt="Model Luxus" />
        <span className="tooltip-content">
          Model Luxus <br />
          Ganz tolle luxuriöse Oberfläche <br />
          Preis: 1000 € <br />
          Lieferbar: ab sofort
        </span>
      </div>
      <div className="model-item">
        <img src={modelWeiss} alt="Model Weiß" />
        <span className="tooltip-content">
          Model Weiß <br />
          Ganz tolle weiße Oberfläche <br />
          Preis: 1000 € <br />
          Lieferbar: ab sofort
        </span>
      </div>
    </div>
  </div>
);

export default ConfiguratorControls;
